html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
}

p {
  font-size: 1.6rem;
  line-height: 1.5;
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

.w-100 {
  width: 80rem;
}

.header {
  background-color: #004269;
  padding: 3rem;
}
@media (min-width: 768px) {
  .header .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  font-size: 3.2rem;
}
@media (min-width: 480px) {
  .header h1 {
    font-size: 4rem;
  }
}
.header h1, .header a, .header p {
  color: #FDFFFC;
  margin: 0;
}
.header .nav-principal {
  display: grid;
  justify-items: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .nav-principal {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .header .nav-principal {
    margin: 0;
  }
}
.header .nav-principal .nav__link {
  padding: 1rem;
  font-size: 1.3rem;
}
@media (min-width: 992px) {
  .header .nav-principal .nav__link {
    font-size: 1.8rem;
  }
}
.header .nav-principal .nav__link:hover {
  color: #99D17B;
  text-decoration: underline;
}

logo p {
  font-size: 1.4rem;
}

.bg-img {
  position: relative;
  background-image: url(../../../build/img/vertical.webp);
  background-size: 100%;
  background-repeat: no-repeat;
  max-height: 70rem;
  min-height: 40rem;
  border: none;
}
@media (min-width: 768px) {
  .bg-img {
    background-image: url(../../../build/img/pexels-christina-morillo-1181555.webp);
    background-size: 100%;
    background-repeat: no-repeat;
    height: 50rem;
  }
}
.bg-img .overlay {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  flex-direction: column;
  justify-content: center;
}

.contenido-imagen {
  color: #FDFFFC;
  font-weight: 700;
  line-height: 1.8;
}
.contenido-imagen h2 {
  margin: 0;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .contenido-imagen h2 {
    font-size: 4.8rem;
  }
}
@media (min-width: 992px) {
  .contenido-imagen h2 {
    font-size: 6rem;
  }
}
.contenido-imagen h2 span {
  color: #99D17B;
}
.contenido-imagen p {
  background-color: #99D17B;
  border-radius: 1rem;
  font-family: "Montserrat", sans-serif;
  display: inline-block;
  color: #000;
  font-size: 1.3rem;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .contenido-imagen p {
    font-size: 2rem;
    padding: 1.5rem 3rem;
  }
}
@media (min-width: 992px) {
  .contenido-imagen p {
    font-size: 2.5rem;
  }
}

.contenido-principal {
  margin-top: 2rem;
}
@media (min-width: 992px) {
  .contenido-principal {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.principal-text h3 {
  margin-bottom: 0;
  margin-top: 2rem;
}
.principal-text p {
  font-size: 1.7rem;
  line-height: 1.5;
}

.principal-img {
  display: grid;
  align-items: center;
}

.servicios {
  margin: 3rem 0 0 0;
  padding: 2rem;
  background-image: url(../../../build/img/bg.webp);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.servicios h3, .servicios h4 {
  text-align: center;
}

.servicios-grid {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .servicios-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    grid-template-rows: 1fr 1fr;
    justify-content: center;
  }
  .servicios-grid:last-child {
    grid-column: 1/3;
  }
}
@media (min-width: 1280px) {
  .servicios-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
    justify-content: center;
    grid-template-rows: 1fr;
  }
}

.servicios__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  width: 30rem;
  padding: 3rem;
}
@media (min-width: 768px) {
  .servicios__contenido {
    padding: 3rem;
    display: grid;
    grid-template-rows: 2fr 1fr;
    justify-content: center;
    align-items: center;
  }
  .servicios__contenido img {
    width: 20rem;
  }
  .servicios__contenido .fastidiosa {
    width: 30rem;
  }
  .servicios__contenido h4 {
    margin-top: 3rem;
  }
}
.servicios__contenido .fastidiosa {
  padding: 0;
}

.contact {
  background-color: #004269;
  padding: 2rem;
  color: #FDFFFC;
}
.contact h3 {
  text-align: center;
}

.form__contact {
  padding: 2rem 2rem 0 2rem;
  border-radius: 1rem;
  background-color: #99D17B;
}
.form__contact fieldset {
  padding: 2rem;
  color: #000;
  border: none;
}
@media (min-width: 1280px) {
  .form__contact fieldset {
    display: grid;
  }
}
.form__contact fieldset legend {
  color: #000;
}

.contenedor__inputs {
  font-weight: 700;
  display: grid;
  gap: 1rem;
  column-gap: 2rem;
  align-items: center;
  line-height: 2;
}
.contenedor__inputs input {
  border-radius: 0.3rem;
  padding: 1rem;
  border: none;
}
@media (min-width: 480px) {
  .contenedor__inputs {
    grid-template-columns: 1fr 6fr;
  }
}
@media (min-width: 768px) {
  .contenedor__inputs {
    grid-template-columns: 1fr 2fr 1fr 2fr;
  }
  .contenedor__inputs .mail {
    grid-column: 2/5;
  }
}

.contenedor__t-area {
  display: grid;
  grid-template-rows: 2, 1fr;
  margin-top: 1rem;
  font-weight: 700;
  gap: 1rem;
  line-height: 1.5;
}
.contenedor__t-area textarea {
  width: 100%;
  border-radius: 0.3rem;
  padding: 1rem;
  border: none;
}

label {
  line-height: 2;
}

.footer {
  background-color: #000;
}

.contenido__footer h3, .contenido__footer p {
  color: #FDFFFC;
}

.database-asegurados {
  margin: 3rem auto;
}

.asegurados {
  margin-top: 3rem;
  width: 100%;
  border-spacing: 0;
  border: 1px solid #000;
}
.asegurados thead {
  border: 1px solid #000;
  background-color: #3A7CA5;
}
.asegurados thead th {
  border: 1px solid #000;
  padding: 1rem;
  color: #FDFFFC;
}
.asegurados td {
  border: 1px solid #000;
  text-align: center;
}