@use '../base/var' as v;
@use '../base/mixins' as m;


.contenido-principal{
    @include m.laptop{
        @include m.grid(2, 2rem)
    };
    
     
    margin-top: 2rem;
}

.principal-text{
    h3{
        margin-bottom: 0;
        margin-top: 2rem;
    }

    p{
        font-size: 1.7rem;
        line-height: 1.5;
    }
}

.principal-img{
    display: grid;
    align-items: center;
}

